import React, { useEffect } from 'react'
import { UIScreenTitles } from '../../../store/ui'
import AddressForm from '../AddressForm/AddressForm'
import ContactDetailsForm from '../ContactDetailsForm/ContactDetailsForm'
import DeliveryDetailsForm from '../DeliveryDetailsForm/DeliveryDetailsForm'
import ProductDetails from '../ProductDetails'
import AccessoriesPicker from '../ScreenAccessoriesPicker/AccessoriesPicker'
import AccessoryDetails from '../ScreenAccessoryDetails'
import ModelDetails from '../ScreenModelDetails'
import ModelPicker from '../ScreenModelPicker/ModelPicker'
import ProductPicker from '../ScreenProductPicker'
import SelectAccessories from '../ScreenSelectAccessories'
import ThankYouScreen from '../ScreenThankYou'

interface props {
  screen: string
  onValidChange: (isValid: boolean, screen: string) => void
}

export function DynamicScreen(props: props) {
  useEffect(() => {
    switch (props.screen) {
      case UIScreenTitles.PRODUCT_DETAILS:
      case UIScreenTitles.MODEL_DETAILS:
      case UIScreenTitles.ACCESSORY_DETAILS:
        props.onValidChange(true, props.screen)
        break
      default:
        break
    }
  }, [props])

  switch (props.screen) {
    case UIScreenTitles.SELECT_PRODUCT:
      return <ProductPicker onValidChange={props.onValidChange} />
    case UIScreenTitles.SELECT_MODEL:
      return <ModelPicker onValidChange={props.onValidChange} />
    case UIScreenTitles.ADD_ACCESSORIES:
      return <AccessoriesPicker />
    case UIScreenTitles.YOUR_DETAILS:
      return <ContactDetailsForm onValidChange={props.onValidChange} />
    case UIScreenTitles.DELIVERY_DETAILS:
      return <DeliveryDetailsForm onValidChange={props.onValidChange} />
    case UIScreenTitles.YOUR_ADDRESS:
      return <AddressForm onValidChange={props.onValidChange} />
    case UIScreenTitles.CONFIRMATION:
      return <ThankYouScreen />
    case UIScreenTitles.MODEL_DETAILS:
      return <ModelDetails />
    case UIScreenTitles.PRODUCT_DETAILS:
      return <ProductDetails />
    case UIScreenTitles.SELECT_ACCESSORIES:
      return <SelectAccessories />
    case UIScreenTitles.ACCESSORY_DETAILS:
      return <AccessoryDetails />
    default:
      return <ProductPicker onValidChange={props.onValidChange} />
  }
}
