import { gql } from 'graphql-request'
export const getProductsQuery = gql`
  {
    products(sort: "rrp") {
      data {
        id
        attributes {
          models {
            data {
              id
            }
          }
          name
          short_description
          description
          rrp
          featured_image {
            data {
              id
              attributes {
                url
                width
                height
              }
            }
          }
          slideshow_images {
            data {
              attributes {
                url
              }
            }
          }
        }
      }
    }
  }
`
export const getModelsQuery = gql`
  {
    models(sort: "rrp") {
      data {
        id
        attributes {
          model_of {
            data {
              id
            }
          }
          included_accessories {
            data {
              id
            }
          }
          optional_accessories {
            data {
              id
            }
          }
          tare_weight_kg
          name
          short_description
          description
          rrp
          featured_image {
            data {
              id
              attributes {
                url
                width
                height
              }
            }
          }
          slideshow_images {
            data {
              attributes {
                url
              }
            }
          }
        }
      }
    }
  }
`

export const getModelsofProductQuery = (productId: string) => gql`
  {
    products(filters: { id: { eq: "${productId}" } }, sort: "rrp" ) {
      data {
        attributes {
          models(sort:"rrp") {
            data {
              id
              attributes {
                  included_accessories {
                    data {
                      id
                    }
                  }
                  optional_accessories {
                    data {
                      id
                    }
                  }
                  tare_weight_kg
                  name
                  short_description
                  description
                  rrp
                  featured_image {
                    data {
                      id
                      attributes {
                        url
                        width
                        height
                      }
                    }
                  }
                  slideshow_images {
                    data {
                      attributes {
                        url
                      }
                    }
                  }
              }
            }
          }
        }
      }
    }
  }
`
