export const getAccessoriesOfModelQuery = (modelId: string) => gql`
  query {
    models(filters: { id: { eq: "${modelId}" } }, sort: "rrp") {
      data {
        id
        attributes {
          optional_accessories {
            data {
              id
              attributes {
                name
                short_description
                description
                rrp
                part_number
                featured_image {
                  data {
                    attributes {
                      url
                    }
                  }
                }
              }
            }
          }
          included_accessories {
            data {
              id
              attributes {
                name
                short_description
                description
                rrp
                part_number
                featured_image {
                  data {
                    attributes {
                      url
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`
export const getCategoriesQuery = () => gql`
  {
    accessoryCategories {
      data {
        id
        attributes {
          name
          accessories {
            data {
              id
            }
          }
        }
      }
    }
  }
`

