import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { config, useSprings } from 'react-spring'
import { usePrefetchImmediately } from '../../../hooks/usePrefetchImmediately'
import { callWebhook, selectBuild, selectBuildModel, selectBuildProduct, setModel } from '../../../store/build'
import { useGetModelsOfProductQuery } from '../../../store/middleware/strapiApi'
import { selectSettings } from '../../../store/settings'
import { showModelDetailsScreen, UIScreenTitles } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading3, Heading4, TextButton } from '../../common/style'
import {
  AnimatedProductWrapper,
  ProductDetails,
  ProductFooter,
  ProductListing,
  ProductThumb,
  ProductThumbWrapper,
  ShortDescription,
} from '../ScreenProductPicker/style'

interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function ModelPicker(props: props) {
  const dispatch = useDispatch()
  const productId = useSelector(selectBuildProduct)?.id
  //fix this
  const { data } = useGetModelsOfProductQuery(productId ?? '')
  let models = data ?? []
  // const models = useSelector(selectProductModels(productId))
  const model = useSelector(selectBuildModel)
  const build = useSelector(selectBuild)
  const settings = useSelector(selectSettings)
  usePrefetchImmediately('getAccessoriesOfModel', model?.id || '1')
  usePrefetchImmediately('getCategories', undefined)

  const springs = useSprings(
    models.length || 0,
    models.map((item, idx) => {
      return {
        from: {
          transform: `translate3d(0px, 40px,  0)`,
          opacity: 0,
        },
        to: {
          transform: `translate3d(0px, 0px, 0)`,
          opacity: 1,
        },
        delay: idx * 100,
        config: config.stiff,
      }
    }),
  )
  //if one of these models is selected, all progress
  useEffect(() => {
    if (models?.find((prod) => prod.id === model?.id)) {
      props.onValidChange(true, UIScreenTitles.SELECT_MODEL)
    } else {
      props.onValidChange(false, UIScreenTitles.SELECT_MODEL)
    }
  })
  let buildItemsMarkup

  if (models) {
    buildItemsMarkup = springs.map((value, idx) => {
      const x = models[idx]
      return (
        <AnimatedProductWrapper
          $isSelected={model?.id === x.id}
          style={value}
          key={idx}
          onClick={() => {
            dispatch(setModel(x.id))
            if (settings?.webhook_url) {
              dispatch(callWebhook(settings?.webhook_url, build))
            }
          }}>
          <ProductThumbWrapper>
            {x.featured_image?.url && (
              <ProductThumb
                className="product-thumb"
                src={process.env.REACT_APP_ASSETS_URL + x.featured_image?.url}
                alt={x.name}
              />
            )}
          </ProductThumbWrapper>
          <ProductDetails>
            <Heading3>{x.name}</Heading3>
            <ShortDescription>{x.short_description}</ShortDescription>
            <ProductFooter>
              {x.rrp && <Heading4 className="product-price">Starts at {formatCurrency(x.rrp)}</Heading4>}
              <TextButton onClick={() => dispatch(showModelDetailsScreen(x.id))}>View Specs</TextButton>
            </ProductFooter>
          </ProductDetails>
        </AnimatedProductWrapper>
      )
    })
  }

  return <ProductListing>{buildItemsMarkup}</ProductListing>
}
