export interface LocalizedAttrs {
  webhook_url: string
  accent_color: string
  email_endpoint: string
  require_user_contact_details_upfront: boolean
  default_product: string | undefined
  default_model: string | undefined
}

declare global {
  export interface Window {
    szReactPlugin: LocalizedAttrs
  }
}

const defaults = {
  webhook_url: '',
  accent_color: '',
  email_endpoint: '',
  require_user_contact_details_upfront: false,
  default_product: 'tvan',
  default_model: 'inspire',
}

const szReactPlugin = window.szReactPlugin || {}
console.log('localized attrs', { ...defaults, ...szReactPlugin })
export const LOCALIZED_ATTRS = { ...defaults, ...szReactPlugin }
