import styled from 'styled-components/macro'

interface theProps {
  isActive?: boolean
}

export const Wrapper = styled.div`
  height: 100%;
  box-sizing: border-box;
`

export const WrapperInner = styled.div`
  display: grid;
  background-color: white;
  height: 100%;

  box-sizing: border-box;
  grid-template-columns: 100%;
  @media ${(p) => p.theme.device.tablet} {
    grid-template-columns: calc(100% - var(--sidebar-width)) var(--sidebar-width);
  }
`
export const SzSwiperWrapper = styled.div`
  max-width: 100%;
`
export const ImageWrapper = styled.figure`
  padding: 0 20px;
  display: flex;
  align-items: center;
  max-width: 100%;
  box-sizing: border-box;
`

export const Sidebar = styled.div<theProps>`
  width: 100%;
  position: ${(p) => (p.isActive ? 'static' : 'absolute')};
  will-change: transform;
  overflow-y: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
  transition: transform 300ms;
  transform: ${(p) => (p.isActive ? 'translateX(0%)' : 'translateX(100%)')};
`

export const SidebarInner = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
`
export const DisplayWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`
export const SidebarWrapper = styled.aside`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 20px 0 0 20px;
  flex-basis: var(--sidebar-width);
  box-sizing: border-box;
  background-color: white;
  @media ${(p) => p.theme.device.tablet} {
    background-color: #eef0f5;
  }
`

export const SidebarBack = styled.div`
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
`
export const ZoomButton = styled.button`
  border: 1px solid var(--grey-2);
  background: transparent;
  height: 45px;
  width: 45px;
  font-size: 22px;
  color: var(--grey-4);
  border-radius: 0;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: var() (--grey-2);
    color: var(--grey-4);
    cursor: pointer;
  }
`

export const SlidesFooter = styled.div`
  display: flex;
  justify-content: center;
`
