import React from 'react'
import { useSelector } from 'react-redux'
import { selectAcc2ById } from '../../../store/catalog'
import './AccessoryDetailsSidebar.css'

interface AccessoryDetailsSidebarProps {
  id: string
}
export default function AccessoryDetailsSidebar(props: AccessoryDetailsSidebarProps) {
  // const dispatch = useDispatch();
  const accessory = useSelector(selectAcc2ById(props.id || ''))
  return (
    <div className="accessory-details-sidebar">
      {accessory?.featured_image?.url && (
        <img
          className="accessory-detail-image"
          src={'' + process.env.REACT_APP_ASSETS_URL + accessory?.featured_image?.url}
          alt=""
        />
      )}
      <h3>{accessory?.name}</h3>
      {accessory?.rrp && <h5>${accessory?.rrp}</h5>}
      <p>{accessory?.description}</p>
    </div>
  )
}
