import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { addAccessory, removeAccessory, selectBuildAccessoryIds, selectBuildModel } from '../../../store/build'
import { newSelectOptionalAccessoriesByCategory } from '../../../store/catalog'
import { useGetAccessoriesOfModelQuery, useGetCategoriesQuery } from '../../../store/middleware/strapiApi'
import { showAccessoryDetailsScreen } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading1 } from '../../common/style'
import FilterSet from '../FilterSet'
import MiniCard from '../MiniCard'
import { AccessoryGrid, CategoryHeading, CategoryWrapper, InnerScreen } from './style'

export default function SelectAccessories() {
  const buildAccessories = useSelector(selectBuildAccessoryIds)
  const dispatch = useDispatch()
  const buildModel = useSelector(selectBuildModel)
  const [filterCat, setFilterCat] = useState('')

  useGetCategoriesQuery()
  useGetAccessoriesOfModelQuery(buildModel?.id || '1')
  const newx = useSelector(newSelectOptionalAccessoriesByCategory)
  // const { data } = useGetAccessoriesOfModelQuery('1')

  const handleFilterChange = (filterCat: string) => {
    setFilterCat(filterCat)
  }

  const catsMarkup = newx
    .filter((cat) => (filterCat === '' ? true : cat.name === filterCat))
    .map((cat, idx) => {
      const accessoryMarkup = cat.accessories.map((accessory) => {
        return (
          <MiniCard
            key={accessory.id}
            image={accessory.featured_image && process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url}
            onDetailsClick={() => {
              dispatch(showAccessoryDetailsScreen(accessory.id))
            }}
            onAddClick={() => dispatch(addAccessory(accessory.id))}
            onRemoveClick={() => dispatch(removeAccessory(accessory.id))}
            title={accessory.name}
            description={formatCurrency(accessory.rrp || 0)}
            showChevron={false}
            isAdded={buildAccessories.find((accId) => accId === accessory.id) !== undefined}
            showAdd={true}
          />
        )
      })
      return (
        <CategoryWrapper key={idx}>
          <CategoryHeading>{cat.name}</CategoryHeading>
          <AccessoryGrid>{accessoryMarkup}</AccessoryGrid>
        </CategoryWrapper>
      )
    }, {})

  return (
    <>
      <InnerScreen>
        <Heading1>Add Accessories</Heading1>
        <FilterSet
          items={newx.filter((cat) => cat.accessories.length !== 0).map((cat) => cat.name)}
          onFilter={handleFilterChange}
        />
        {catsMarkup}
      </InnerScreen>
    </>
  )
}
