import { createEntityAdapter } from '@reduxjs/toolkit'
import { createApi } from '@reduxjs/toolkit/query/react'
import { GraphQLClient } from 'graphql-request'
import {
  getAccessoriesOfModelByNameQuery,
  getAccessoriesOfModelQuery,
  getCategoriesQuery,
  getModelsofProductByNameQuery,
  getModelsofProductQuery,
  getModelsQuery,
  getProductsQuery,
} from '../queries'
import { Accessory, Category, Model, Product } from '../types'
import { normalize } from './../../utils/utils'

const graphqlBaseQuery =
  ({ baseUrl }: { baseUrl: string }) =>
  async ({ body }: { body: string }) => {
    try {
      const graphQLClient = new GraphQLClient(baseUrl, {
        // headers: {
        //   authorization: 'Bearer MY_TOKEN',
        // },
      })
      const result = await graphQLClient.request(body)
      return { data: result }
    } catch (error) {
      // if (error instanceof ClientError) {
      //   return { error: { status: error.response.status, data: error } }
      // }
      return { error: { status: 500, data: error } }
    }
  }

export const modelAdapter = createEntityAdapter<Model>({
  selectId: (model) => model.id,
  sortComparer: (a, b) => a.name.localeCompare(b.name),
})
// https://www.reddit.com/r/reduxjs/comments/pd93b6/rtk_query/
export const strapiApi = createApi({
  reducerPath: 'strapiApi',
  baseQuery: graphqlBaseQuery({ baseUrl: process.env.REACT_APP_GRAPHQL_URL + '' }),
  endpoints: (builder) => ({
    getProducts: builder.query<Product[], void>({
      query: () => ({
        body: getProductsQuery,
      }),
      transformResponse: (response) => {
        return normalize(response.products)
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/productsReceived', payload: data })
        } catch (err) {
          console.log('err in getProducts')
        }
      },
    }),
    getModels: builder.query<Model[], void>({
      query: () => ({
        body: getModelsQuery,
      }),
      transformResponse: (response) => {
        return normalize(response.models)
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/modelsReceived', payload: data })
        } catch (err) {
          console.log('err in getModels')
        }
      },
    }),
    getModelsOfProduct: builder.query<Model[], string>({
      query: (id) => ({
        body: getModelsofProductQuery(id),
      }),
      transformResponse: (response) => {
        return normalize(response.products.data[0].attributes.models)
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/modelsReceived', payload: data })
        } catch (err) {
          console.log('err in getModelsOfProduct')
        }
      },
    }),
    getModelsOfProductByName: builder.query<Model[], string>({
      query: (name) => ({
        body: getModelsofProductByNameQuery(name),
      }),
      transformResponse: (response) => {
        return normalize(response.products.data[0].attributes.models)
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/modelsReceived', payload: data })
        } catch (err) {
          console.log('err in getModelsOfProductByName')
        }
      },
    }),
    getAccessoriesOfModel: builder.query<Accessory[], string>({
      query: (id) => ({
        body: getAccessoriesOfModelQuery(id),
      }),
      transformResponse: (response) => {
        return [
          ...normalize(response.models.data[0].attributes.optional_accessories),
          ...normalize(response.models.data[0].attributes.included_accessories),
        ]
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/accessoriesReceived', payload: data })
        } catch (err) {
          console.log('err in getAccessoriesOfModel')
        }
      },
    }),
    getAccessoriesOfModelByName: builder.query<Accessory[], string>({
      query: (name) => ({
        body: getAccessoriesOfModelByNameQuery(name),
      }),
      transformResponse: (response) => {
        return [
          ...normalize(response.models.data[0].attributes.optional_accessories),
          ...normalize(response.models.data[0].attributes.included_accessories),
        ]
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/accessoriesReceived', payload: data })
        } catch (err) {
          console.log('err in getAccessoriesOfModelByName')
        }
      },
    }),
    getCategories: builder.query<Category[], void>({
      query: () => ({
        body: getCategoriesQuery(),
      }),
      transformResponse: (response) => {
        return normalize(response.accessoryCategories)
      },
      async onQueryStarted(arg, { dispatch, queryFulfilled }) {
        try {
          const { data } = await queryFulfilled
          dispatch({ type: 'catalog/categoriesReceived', payload: data })
        } catch (err) {
          console.log('err in getCategories')
        }
      },
    }),
  }),
})
export const {
  useGetModelsQuery,
  useGetProductsQuery,
  useGetModelsOfProductQuery,
  useGetAccessoriesOfModelQuery,
  useGetCategoriesQuery,
  useGetAccessoriesOfModelByNameQuery,
  useGetModelsOfProductByNameQuery,
} = strapiApi
