import React, { useRef } from 'react'
import { VscZoomIn, VscZoomOut } from 'react-icons/vsc'
import ReactMarkdown from 'react-markdown'
import { useSelector } from 'react-redux'
import { A11y, Navigation, Pagination, Zoom } from 'swiper'
import 'swiper/css'
import 'swiper/css/lazy'
import 'swiper/css/navigation'
import 'swiper/css/pagination'
import 'swiper/css/zoom'
import { Swiper, SwiperSlide } from 'swiper/react'
import { selectMod2ById } from '../../../store/catalog'
import { selectDisplayedId } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading2 } from '../../common/style'
import { ZoomButton } from '../ScreenAccessoriesPicker/style'
import { ImageWrapper, Price, ProductImage, ProductInfo, Screen, SlidesFooter } from './../ProductDetails/style'

export default function ModelDetails() {
  const id = useSelector(selectDisplayedId)
  const model = useSelector(selectMod2ById(id))
  const slides = model?.slideshow_images || []
  const ref = useRef<HTMLDivElement>(null)

  return (
    <Screen>
      <ImageWrapper>
        {slides ? (
          <>
            <Swiper
              /* @ts-ignore */
              ref={ref}
              modules={[Navigation, Pagination, A11y, Zoom]}
              preloadImages={false}
              navigation
              pagination={{ clickable: true }}
              spaceBetween={0}
              slidesPerView={1}
              zoom
              lazy
              onSlideChange={() => console.log('slide change')}
              onSwiper={(swiper) => console.log(swiper)}>
              {slides?.map((slide, idx) => (
                <SwiperSlide zoom key={idx}>
                  <img src={slide.url} alt="" />
                </SwiperSlide>
              ))}
            </Swiper>
            <SlidesFooter>
              <ZoomButton
                onClick={() => {
                  /* @ts-ignore */
                  ref.current.swiper.zoom.out()
                }}>
                <VscZoomOut />
              </ZoomButton>
              <ZoomButton
                onClick={() => {
                  /* @ts-ignore */
                  ref.current.swiper.zoom.in()
                }}>
                <VscZoomIn />
              </ZoomButton>
            </SlidesFooter>
          </>
        ) : (
          <ProductImage src={'' + process.env.REACT_APP_ASSETS_URL + model?.featured_image?.url} alt="" />
        )}
      </ImageWrapper>
      <ProductInfo>
        <Heading2>{model && model?.name}</Heading2>
        <Price>Starts at {model?.rrp && formatCurrency(model.rrp)}</Price>
        {model?.description && <ReactMarkdown>{model?.description + ''}</ReactMarkdown>}
      </ProductInfo>
    </Screen>
    // <DetailsWrapper>
    //   <Col>{model && <img src={'' + process.env.REACT_APP_ASSETS_URL + model?.featured_image?.url} alt="" />}</Col>
    //   <Col>
    //     <Heading3>{model && model?.name}</Heading3>
    //     <Heading4>{model?.rrp && formatCurrency(model.rrp)}</Heading4>
    //     {model && model.description}
    //   </Col>
    // </DetailsWrapper>
  )
}
