import { createEntityAdapter, createSlice, EntityId, EntityState, PayloadAction } from '@reduxjs/toolkit'
import { TypedUseSelectorHook, useSelector } from 'react-redux'
import { apiCallBegan } from './middleware/api'
import { AppThunk, RootState } from './store'
import { Accessory, Category, Image, Model, Product } from './types'

export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector

export interface CustomOption {
  id: string
  option_name: string
  option_description?: string
  rrp?: number
  option_image?: Image[]
}

export interface CustomOptionPicker {
  id: string
  screen_name: string
  option_name: string
  options: CustomOption[]
}

export const productAdapter = createEntityAdapter<Product>({ sortComparer: (a, b) => (a.rrp || 0) - (b.rrp || 0) })
const prodInitial = productAdapter.getInitialState()

export const modelAdapter = createEntityAdapter<Model>({ sortComparer: (a, b) => (a.rrp || 0) - (b.rrp || 0) })
const modelInitial = modelAdapter.getInitialState()

export const accessoriesAdapter = createEntityAdapter<Accessory>({
  sortComparer: (a, b) => (a.rrp || 0) - (b.rrp || 0),
})
const accessoryInitial = accessoriesAdapter.getInitialState()

export const categoriesAdapter = createEntityAdapter<Category>({
  sortComparer: (a, b) => a.name.localeCompare(b.name),
})
const categoriesInitial = categoriesAdapter.getInitialState()

interface catalog {
  models: EntityState<Model>
  products: EntityState<Product>
  accessories: EntityState<Accessory>
  categories: EntityState<Category>
  customOptionPickersLoading: boolean
  accessoryCategories: Category[]
  customOptionPickers: CustomOptionPicker[]
}

const initialState: catalog = {
  customOptionPickersLoading: false,
  products: prodInitial,
  models: modelInitial,
  accessories: accessoryInitial,
  categories: categoriesInitial,
  accessoryCategories: [],
  customOptionPickers: [],
}

export const catalogSlice = createSlice({
  name: 'catalog',
  initialState,
  reducers: {
    categoriesReceived: (state, action: PayloadAction<any>) => {
      state.categories = categoriesAdapter.upsertMany(state.categories, action.payload)
    },
    accessoriesReceived: (state, action: PayloadAction<any>) => {
      state.accessories = accessoriesAdapter.upsertMany(state.accessories, action.payload)
    },
    modelsReceived: (state, action: PayloadAction<any>) => {
      state.models = modelAdapter.upsertMany(state.models, action.payload)
    },
    productsReceived: (state, action: PayloadAction<any>) => {
      state.products = productAdapter.upsertMany(state.products, action.payload)
    },
    customOptionPickersRequested: (state, action: PayloadAction<string>) => {
      state.customOptionPickersLoading = true
    },
    customOptionPickersReceived: (state, action: PayloadAction<any>) => {
      if (action.payload.model) {
        state.customOptionPickers = action.payload.model.other_options
      }
      state.customOptionPickersLoading = false
    },
  },
})

export const loadCustomOptionPickers = (): AppThunk => (dispatch, getState) => {
  const modelId = getState().build.modelId || 0
  const graphQlQuery = /*GRAPHQL*/ `{
  model(id: "${modelId}") {
    other_options{
      id
      screen_name
      option_name
      options {	
        id
        option_name
        option_description
        rrp
        option_image {
          url
        }
      }
    }  
  }
}`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + '',
      graphQlQuery,
      onStart: catalogSlice.actions.customOptionPickersRequested.type,
      onSuccess: catalogSlice.actions.customOptionPickersReceived.type,
    }),
  )
}

// SELECTORS
export const {
  selectAll: selectAllProducts,
  selectById: selectProdById,
  selectEntities: selectProductEntities,
  selectIds: selectProductIds,
} = productAdapter.getSelectors<RootState>((state) => state.catalog.products ?? prodInitial)

export const { selectAll: selectAllModels, selectById: selectModById } = modelAdapter.getSelectors<RootState>(
  (state) => state.catalog.models ?? modelInitial,
)
export const selectMod2ById = (id: string) => (state: RootState) => selectModById(state, id)

export const { selectAll: selectAllAccessories, selectById: selectAccById } =
  accessoriesAdapter.getSelectors<RootState>((state) => state.catalog.accessories ?? accessoryInitial)

export const selectAcc2ById = (id: string) => (state: RootState) => selectAccById(state, id)

export const { selectAll: selectAllCategories, selectById: selectCategoryById } =
  categoriesAdapter.getSelectors<RootState>((state) => state.catalog.categories ?? categoriesInitial)

export const selectProductModels = (prodId: EntityId | undefined) => (state: RootState) => {
  if (prodId == undefined) return [] as Model[]

  const modelIds = selectProdById(state, prodId)?.models ?? []
  return modelIds.flatMap((idobj) => {
    const model = selectModById(state, idobj.id)
    return model ? model : []
  })
}

export const selectProductByName = (name: string | undefined) => (state: RootState) =>
  selectAllProducts(state).find((prod) => prod.name.toLowerCase() === name?.toLocaleLowerCase())

export const selectModelByName = (name: string | undefined) => (state: RootState) =>
  selectAllModels(state).find((model) => model.name.toLowerCase() === name?.toLocaleLowerCase())

export const selectProd2ById = (id: string) => (state: RootState) => selectProdById(state, id)

export const newSelectOptionalAccessoriesByCategory = (state: RootState) => {
  if (!state.build.modelId) return []

  const filterUndefined = (accessories: (Accessory | undefined)[]) =>
    accessories.filter((acc): acc is Accessory => acc !== undefined)

  const filterEmptyCats = (cats: { name: string; accessories: Accessory[] }[]) =>
    cats.filter((cat) => cat.accessories.length > 0)

  const intersection = (accessoriesA: { id: string }[], accessoriesB: { id: string }[]) =>
    accessoriesA.filter((acc_a) => accessoriesB.filter((acc_b) => acc_a.id === acc_b.id).length > 0)

  const getAccessoriesFromIds = (ids: { id: string }[]) => ids.map((id) => selectAccById(state, id.id))

  const optional_accessories = selectModById(state, state.build.modelId)?.optional_accessories ?? []

  return filterEmptyCats(
    selectAllCategories(state).map((cat) => {
      return {
        name: cat.name,
        accessories: filterUndefined(getAccessoriesFromIds(intersection(optional_accessories, cat.accessories))),
      }
    }),
  )
}

export const selectCustomOptionPicker = (id: string) => (state: RootState) =>
  state.catalog.customOptionPickers.find((el) => el.id === id)
export const selectCustomOptionPickers = (state: RootState) => state.catalog.customOptionPickers
export const selectCustomOptionPickersLoading = (state: RootState) => state.catalog.customOptionPickersLoading

