import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { usePrefetchImmediately } from '../../../hooks/usePrefetchImmediately'
import { selectBuildProduct, setProduct } from '../../../store/build'
import { selectAllProducts } from '../../../store/catalog'
import { useGetProductsQuery } from '../../../store/middleware/strapiApi'
import { showProductDetailsScreen, UIScreenTitles } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Heading3, Heading4, TextButton } from '../../common/style'
import {
  ProductDetails,
  ProductFooter,
  ProductListing,
  ProductThumb,
  ProductThumbWrapper,
  ProductWrapper,
  ShortDescription,
} from './style'

interface props {
  onValidChange: (isValid: boolean, screen: string) => void
}

export default function ProductPicker(props: props) {
  const dispatch = useDispatch()
  useGetProductsQuery()
  const products = useSelector(selectAllProducts)
  const product = useSelector(selectBuildProduct)
  usePrefetchImmediately('getModelsOfProduct', product?.id || '2')

  useEffect(() => {
    props.onValidChange(!!products.find((prod) => prod.id === product?.id), UIScreenTitles.SELECT_PRODUCT)
  }, [product, products, props])

  let buildItemsMarkup
  if (products) {
    buildItemsMarkup = products.map((products) => {
      return (
        <ProductWrapper
          $isSelected={product?.id === products.id}
          key={products.id}
          onClick={() => {
            dispatch(setProduct(products.id))
          }}>
          <ProductThumbWrapper>
            {products.featured_image?.url && <ProductThumb alt={products.name} src={products.featured_image?.url} />}
          </ProductThumbWrapper>
          <ProductDetails>
            <Heading3>{products.name}</Heading3>
            <ShortDescription>{products.short_description}</ShortDescription>
            <ProductFooter>
              {products.rrp && <Heading4>Starts at {formatCurrency(products.rrp)}</Heading4>}
              <TextButton onClick={() => dispatch(showProductDetailsScreen(products.id))}>View Specs</TextButton>
            </ProductFooter>
          </ProductDetails>
        </ProductWrapper>
      )
    })
  }

  return <ProductListing>{buildItemsMarkup}</ProductListing>
}
