import { PrefetchOptions } from '@reduxjs/toolkit/dist/query/core/module'
import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { strapiApi } from '../store/middleware/strapiApi'

type EndpointNames = keyof typeof strapiApi.endpoints

export function usePrefetchImmediately<T extends EndpointNames>(
  endpoint: T,
  arg: Parameters<typeof strapiApi.endpoints[T]['initiate']>[0],
  options: PrefetchOptions = {},
) {
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(strapiApi.util.prefetch(endpoint, arg as any, options))
  }, [])
}
