import React from 'react'
import { VscArrowRight } from 'react-icons/vsc'
import { Button } from './../../common/style'
import { Container, Wrapper } from './style'
interface FooterNavProps {
  onNextClick: () => void
  onPrevClick: () => void
  showPrev: boolean
  showNext: boolean
  prevEnabled: boolean
  nextEnabled: boolean
  prevLabel: string
  nextLabel: string
}
export default function FooterNav(props: FooterNavProps) {
  const { showPrev, showNext, onPrevClick, onNextClick, prevEnabled, nextEnabled, prevLabel, nextLabel } = props

  return (
    <Wrapper>
      <Container>
        {showPrev && (
          <Button secondary disabled={!prevEnabled} onClick={onPrevClick}>
            {prevLabel}
          </Button>
        )}
        {showNext && (
          <Button disabled={!nextEnabled} onClick={onNextClick}>
            {nextLabel} {nextLabel !== 'Done' && <VscArrowRight />}
          </Button>
        )}
      </Container>
    </Wrapper>
  )
}
